import React, {Fragment} from 'react';
import PropTypes from 'prop-types';
import {Avatar, Button,Cell} from '@vkontakte/vkui';

const urlUser = 'https://vk.com/id'

const description = (fetchedUser) => (
	<Fragment>
		{fetchedUser.status && fetchedUser.status ? fetchedUser.status : ''}
		{fetchedUser.status && <div style={{ 'height': '7px'}} /> }
		{fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
	</Fragment>
);

const User = ({handleBtnOne, btnOneTitle, handleBtnTwo, btnTwoTitle, fetchedUser}) => (
	<Fragment>
		{fetchedUser &&
		<Cell
			before={fetchedUser.photo_200 || fetchedUser.photo_200_orig ? 
					<Avatar
						src={fetchedUser.photo_200 || fetchedUser.photo_200_orig}
					/> : null}
			size="l"
			description={description(fetchedUser)}
			onClick={(e) => window.open(`${urlUser}${fetchedUser.id}`)}
			bottomContent={
				(handleBtnOne || handleBtnTwo) &&
				<div style={{ display: 'flex' }}>
					{handleBtnOne && <Button size="m" onClick={(e) => handleBtnOne(fetchedUser.id)} style={{ marginRight: 8 }}>{btnOneTitle}</Button>}
					{handleBtnTwo && <Button size="m" onClick={(e) => handleBtnTwo(fetchedUser.id)} level="secondary">{btnTwoTitle}</Button>}
				</div>
			}
		>
		{`${fetchedUser.first_name} ${fetchedUser.last_name}`}</Cell>}
	</Fragment>
);

User.propTypes = {
	handleBtnOne: PropTypes.func,
	btnOneTitle: PropTypes.string,
	handleBtnTwo: PropTypes.func,
	btnTwoTitle: PropTypes.string,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		photo_200_orig: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		status: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default User;
