import * as types from '../actionsType';
import Immutable from 'seamless-immutable';

const initialState = Immutable([]);

export default function friends(state = initialState, actions = {}) {
	switch(actions.type) {
		case types.SET_FRIENDS_DATA:
			return [].concat(state, actions.fiendsData);
		case types.DELETE_FRIEND:
			return state.filter(frend => frend.id !== actions.id);
		default:
			return state;
	}
}

export function getFriends (state) {
	return state.friends;
}