import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { Panel, Group, PanelHeader, Cell, List} from '@vkontakte/vkui';

import User from '../components/User/User';

class HomeContainer extends Component{
	render () {
		const {id, go, fetchedUser} = this.props;
		return (
			<Panel id={id}>
				<PanelHeader> Главная </PanelHeader>
				<List>
					<User fetchedUser={fetchedUser} />
				</List>
				<Group title="Управление друзьями">
					<List>
						<Cell expandable onClick={go} data-to='friends'>Удалить друзей</Cell>
						<Cell expandable onClick={go} data-to='friends'>Друзья в инстаграме</Cell>
						<Cell expandable onClick={go} data-to='friends'>Последняя активность</Cell>
					</List>
				</Group>
			</Panel>
		);
	}
}

HomeContainer.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		status: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};
export default HomeContainer;
