import React, {Component} from 'react';
import {connect} from 'react-redux';

import * as userSelects from './store/user/reducer';
import * as userActions from './store/user/action';

import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './containers/HomeContainer';
import Friends from './containers/Friends/FriendsContainer';

class App extends Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home'
		};
	}

	componentDidMount() {
		const { dispatch } = this.props;
		dispatch(userActions.fetchUser());
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		const {activePanel} = this.state;
		const {fetchedUser} = this.props;
		return (
			<View activePanel={activePanel}>
				<Home id='home' go={this.go} fetchedUser={fetchedUser} />
				<Friends id='friends' go={this.go} />
			</View>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		fetchedUser: userSelects.getUser(state),
	}
}

export default connect(mapStateToProps, null)(App);
