import React, {Component} from 'react';
import {connect} from 'react-redux';

import { Panel, Group, PanelHeader, Cell, List, platform, HeaderButton, IOS, View} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import '@vkontakte/vkui/dist/vkui.css';

import * as frierndsSelects from '../../store/friernds/reducer';
import * as frierndsActions from '../../store/friernds/action';

import User from '../../components/User/User';

class FrierndsContainer extends Component {
	osname = platform();
	constructor(props) {
		super(props);
	}

	componentDidMount() {
		const { dispatch, fetchedFriernds } = this.props;
		const {fetchFriernds} = frierndsActions;

		if (!fetchedFriernds.length) {
			dispatch(fetchFriernds());
		}
	}

	delFriend (id) {
		const {dispatch, deleteFriernd} = this.props;
		dispatch(deleteFriernd(id));
	}

	render() {
		const {id, go, fetchedFriernds} = this.props;
		return (
			<Panel id={id}>
				<PanelHeader
					left={<HeaderButton onClick={go} data-to="home">
						{this.osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
					</HeaderButton>}
				> Друзья </PanelHeader>
				<List>
				{fetchedFriernds && fetchedFriernds.map(fetchedUser => (
					<User
						handleBtnTwo={(id) => this.delFriend(id)}
						btnTwoTitle='Удалить'
						key={fetchedUser.id}
						fetchedUser={fetchedUser}
					/>
				))}
				</List>
			</Panel>
		);
	}
}

const mapStateToProps = (state) => {
	const {getFriends} = frierndsSelects;
	return {
		fetchedFriernds: getFriends(state)
	}
};

const mapDispatchToProps = (dispatch) => {
	const {deleteFriernd} = frierndsActions;
	return {
		dispatch,
		deleteFriernd
	}
};

export default connect(mapStateToProps, mapDispatchToProps)(FrierndsContainer);
