import axios from 'axios';

const access_token = '297cbfb44eb10d05d05281a0b8d5b4244d3b7efe4c5fdc626360f9d4dda14a82a428e42ebc658dac5c707';
const url = 'https://api.vk.com/method/';
const v = 'v=5.80';

const getProfilesUrl = (method, opt) => {
	opt.access_token = access_token;
	let urlOpts = '';
	let optKeys = Object.keys(opt);
	optKeys.forEach(item => urlOpts += `${item}=${opt[item]}&`);
	return `${url}${method}?${urlOpts}${v}`;
};

export const vk = async (method, opt) => {
	try {
		const res = await axios.get(getProfilesUrl(method, opt));
		if (res.data.error) {
			throw new Error(res.data.error.error_msg);
		}
		return await res.data.response;
	} catch (error) {
		throw new Error(error);
	}
};

export const getUser = async () => {
	const method = 'users.get';
	const opts = {
		fields: 'photo_id, verified, sex, bdate, city, country, home_town, has_photo, photo_50, photo_100, photo_200_orig, photo_200, photo_400_orig, photo_max, photo_max_orig, online, domain, has_mobile, contacts, site, education, universities, schools, status, last_seen, followers_count, common_count, occupation, nickname, relatives, relation, personal, connections, exports, activities, interests, music, movies, tv, books, games, about, quotes, can_post, can_see_all_posts, can_see_audio, can_write_private_message, can_send_friend_request, is_favorite, is_hidden_from_feed, timezone, screen_name, maiden_name, crop_photo, is_friend, friend_status, career, military, blacklisted, blacklisted_by_me, can_be_invited_group'
	}
	const res = await vk(method, opts);
	return await res[0];
};

export const getFriends = async (user_id='', count=100, offset=0) => {
	const method = 'friends.get';
	const opts ={
		user_id,
		count,
		offset,
		fields: 'nickname, domain, sex, bdate, city, country, timezone, photo_50, photo_100, photo_200_orig, has_mobile, contacts, education, online, relation, last_seen, status, can_write_private_message, can_see_all_posts, can_post, universities',

	};
	const res = await vk(method, opts);
	return await res.items;
};

export const deleteFriend = async (id) => {
	const method = 'friends.delete';
	const opts = { user_id: id };
	return await vk(method, opts);
};