import * as types from '../actionsType';
import api from '../../libs/api/api';

export const fetchFriernds = () => async (dispatch, getState) => {
	try {
		const fiendsData = await api.vk.getFriends();
		dispatch({type: types.SET_FRIENDS_DATA, fiendsData});
	} catch(error) {
		console.error(error);
	}
};

export const deleteFriernd = (id) => async (dispatch, getState) => {
	try {
		await api.vk.deleteFriend(id);
		dispatch({type: types.DELETE_FRIEND, id});
	} catch(error) {
		console.error(error);
	}
};